<script>
import { Pie } from 'vue-chartjs'

export default {
  extends: Pie,
  props: ['labels', 'dataPoints'],
  mounted () {
    this.renderChart({
      labels: this.labels,
      datasets: [
        {
          backgroundColor: [
            '#41B883',
            '#E46651',
            '#00D8FF',
            '#DD1B16',
            '#00BCD4', '#03A9F4', '#009688', '#673AB7', '#3F51B5', '#2196F3', '#F44336', '#E91E63',
            '#9C27B0', '#4CAF50', '#FFEB3B', '#CDDC39', '#FFC107', '#FF9800', '#FF5722', '#795548', '#607D8B', '#9E9E9E'
          ],
          data: this.dataPoints
        }
      ]
    }, {responsive: true, maintainAspectRatio: false})
  }
}
</script>



